@import url(https://fonts.googleapis.com/css?family=Baloo+Tamma+2|Monoton&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  font-size: 62.5%;
}

body {
  width: 100%;
  height: 100vh;
}



.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(346deg, rgba(55, 55, 55,0.04) 0%, rgba(55, 55, 55,0.04) 22%,rgba(140, 140, 140,0.04) 22%, rgba(140, 140, 140,0.04) 69%,rgba(225, 225, 225,0.04) 69%, rgba(225, 225, 225,0.04) 100%),linear-gradient(31deg, rgba(55, 55, 55,0.04) 0%, rgba(55, 55, 55,0.04) 42%,rgba(140, 140, 140,0.04) 42%, rgba(140, 140, 140,0.04) 85%,rgba(225, 225, 225,0.04) 85%, rgba(225, 225, 225,0.04) 100%),linear-gradient(55deg, rgba(55, 55, 55,0.04) 0%, rgba(55, 55, 55,0.04) 13%,rgba(140, 140, 140,0.04) 13%, rgba(140, 140, 140,0.04) 72%,rgba(225, 225, 225,0.04) 72%, rgba(225, 225, 225,0.04) 100%),linear-gradient(90deg, rgb(0,0,0),rgb(0,0,0));
}

.App-banner {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 12vmin;
  background-color: #fcba03;
  font-family: 'Monoton', sans-serif;
  font-size: 8vmin;
  letter-spacing: 1.5vmin;
  display: flex;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
}

.Cell-on {
    background-color: #fcba03;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    border-radius: 50%;
}

.Cell-off {
    background-color: #191A1A;
    -webkit-transition: all 0.8s ease-in-out;
    transition: all 0.8s ease-in-out;
    border-radius: 50%;
}
.Grid {
    display: grid;
    grid-template-columns: repeat(5, 12vmin);
    grid-template-rows: repeat(5, 12vmin);
    grid-gap: 2vmin;
    gap: 2vmin;
}

.Grid-win {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Grid-win-message {
    font-size: 10vmin;
    font-family: 'Baloo Tamma 2', sans-serif;
    color: white;
}

.Grid-win-button {
    font-size: 4vmin;
    background-color: #fcba03;
    width: 35vmin;
    height: 10vmin;
    border: none;
    outline: none;
    font-family: 'Baloo Tamma 2';
    cursor: pointer;
}
